*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;

}
body{
    background-color: #e0e8ff;
}
main{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
text-align: center;
display: flex;
flex-direction: column;
align-items: center;
background-color: #fff;
width: 90vw;
max-width: 500px;
margin: 0 auto ;
margin-top: 50px;
padding-bottom: 20px;


}
h1{
  background-color: #1b1096;  
  color: #fff;
  width: 100%;
  margin-bottom: 10px;
  font-size: 2rem;
  padding: 20px 10px;
}
.input-section{
    width: 70%;
}
.input-slider{
    width: 100%;
    margin-top: 5px;
}
output{
    padding: 10px 5px;
    border-radius: 10px;
    background-color: #1b1096 ;
    color: #fff;
    margin-top: 10px;
}
.output-section{

margin: 20px;

}




